import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AuthService } from 'angularx-social-login';
import { ReviewFormComponent, ReviewModel } from '../review-form/review-form.component';

@Component({
  selector: 'app-uibutton',
  templateUrl: './uibutton.component.html',
  styleUrls: ['./uibutton.component.css']
})
export class UibuttonComponent implements OnInit {

  constructor(public dialog: MatDialog, private authService: AuthService) { }

  reviewCard: ReviewModel = null;

  ngOnInit() {
  }

  openReviewPopup() {
    const dialogRef = this.dialog.open(ReviewFormComponent, { disableClose: true });

    dialogRef.afterClosed().subscribe(result => {
      this.reviewCard = result;
    });
  }

  signOut(): void {
    this.authService.signOut();
    this.reviewCard = null;
  }

}
