import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';

export interface CategoryModel {
  imageurl: string;
  festcategory: string;
  festurl: string;
}

@Component({
  selector: 'app-fest-category',
  templateUrl: './fest-category.component.html',
  styleUrls: ['./fest-category.component.css']
})
export class FestCategoryComponent implements OnInit {

  constructor(private http: HttpClient) { }

  itemList: CategoryModel[] = [];

  ngOnInit() {
    this.http.get<CategoryModel[]>('../assets/data1.json')
    .subscribe((data) => {
      this.itemList = data;
    });
  }


}
