import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Injector, DoBootstrap } from '@angular/core';
import { createCustomElement } from '@angular/elements';

import { AppComponent } from './app.component';
import { ImageListComponent } from './image-list/image-list.component';
import { HttpClientModule } from '@angular/common/http';
import { TrustpilotCarousalComponent } from './trustpilot-carousal/trustpilot-carousal.component';
import { EditorsPickComponent } from './editors-pick/editors-pick.component';
import { SearchPanelComponent } from './search-panel/search-panel.component';
import { UicardComponent } from './uicard/uicard.component';
import { LoadSpinnerComponent } from './load-spinner/load-spinner.component';
import { BannerListComponent } from './banner-list/banner-list.component';
import { FestCategoryComponent } from './fest-category/fest-category.component';
import { UibuttonComponent } from './uibutton/uibutton.component';
import { ReviewFormComponent } from './review-form/review-form.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatButtonModule, MatDialogModule, MatFormFieldModule, MatSelectModule } from '@angular/material';
import {
  AuthServiceConfig,
  GoogleLoginProvider,
  SocialLoginModule,
} from 'angularx-social-login';
import { FormsModule } from '@angular/forms';

const config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider('705097058169-ak705dhikst1kvpp6lvo64cupnfiasnr.apps.googleusercontent.com'),
  }
]);
export function provideConfig() {
  return config;
}

@NgModule({
  declarations: [
    AppComponent,
    ImageListComponent,
    TrustpilotCarousalComponent,
    EditorsPickComponent,
    SearchPanelComponent,
    UicardComponent,
    LoadSpinnerComponent,
    BannerListComponent,
    FestCategoryComponent,
    UibuttonComponent,
    ReviewFormComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatFormFieldModule,
    MatSelectModule,
    MatButtonModule,
    MatDialogModule
  ],
  providers: [
    {
      provide: AuthServiceConfig,
      useFactory: provideConfig,
    },
  ],
  entryComponents: [
    ImageListComponent,
    TrustpilotCarousalComponent,
    EditorsPickComponent,
    SearchPanelComponent,
    UicardComponent,
    LoadSpinnerComponent,
    BannerListComponent,
    FestCategoryComponent,
    UibuttonComponent,
    ReviewFormComponent
  ]
})
export class AppModule implements DoBootstrap {
  constructor(private injector: Injector) {
    const webComponent = createCustomElement(ImageListComponent, {injector: this.injector});
    customElements.define('ui-main-image', webComponent);

    const webComponent1 = createCustomElement(TrustpilotCarousalComponent, {injector: this.injector});
    customElements.define('ui-trustpilot-carousal', webComponent1);

    const webComponent2 = createCustomElement(SearchPanelComponent, {injector: this.injector});
    customElements.define('ui-search-panel', webComponent2);

    const webComponent3 = createCustomElement(UicardComponent, {injector: this.injector});
    customElements.define('ui-review-card', webComponent3);

    const webComponent4 = createCustomElement(BannerListComponent, {injector: this.injector});
    customElements.define('ui-banner-list', webComponent4);

    const webComponent5 = createCustomElement(FestCategoryComponent, {injector: this.injector});
    customElements.define('ui-fest-category', webComponent5);

    const webComponent6 = createCustomElement(UibuttonComponent, {injector: this.injector});
    customElements.define('ui-addreview-button', webComponent6);

    const webComponent7 = createCustomElement(EditorsPickComponent, {injector: this.injector});
    customElements.define('ui-editors-pick', webComponent7);
  }
  ngDoBootstrap(): void {}
}
